@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar-brand {
padding-top: 12px;
font-family: 'Righteous', sans-serif !important;
text-transform: uppercase;
}
.hero {
    background-size: 400% 400%;
    animation: 15s ease infinite;
    min-height: 60vh;
    padding: 50px 0;
    width: 100%;
    background: linear-gradient(45deg,#F17C58, #E94584, #24AADB , #27DBB1, #74b9ff, #a29bfe);
    background-size: 600% 100%;
    animation: gradient 16s linear infinite;
    animation-direction: alternate;
}
@keyframes gradient {
    0% {background-position: 0%}
    100% {background-position: 100%}
}
/* HERO SECTION */
.hero-text {
    width: 80%;
    padding: 15px;
    margin: 90px auto;
    text-align: center;
}
.hero-text h1 {
    color: white;
    font-size: 2.2em;

    margin-bottom: 0;
}
.hero-text .lead {
    margin-top: 0;
    color: white;
    font-size: 22px;
}
.hero .fa-icons {
    color: #ffffff;
    padding: 8px;
    border: 1px solid #ffffff;
    border-radius: 50%;
    transition: 0.5s;
    margin: 0 5px;
}
.hero .fa-icons:hover {
    color: #2980b9;
    background-color: #ffffff;
    transition: 0.5s;
}
.hero img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    transition: 0.5s;
}
.hero img:hover {
    transition: 0.5s;
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}

/* PROJECTS/PORTFOLIO */
.projects {
    text-align: center;
}

.projects img {
    width: 80%;
    margin: 0 auto;
}
.projects .row {
    margin-bottom: 20px;
}

/* MORE ABOUT ME & SKILLS */

h1, h2, h3, h4 {
    font-family: 'Righteous', sans-serif !important;
    text-transform: uppercase;
    padding: 15px;
}

.skills {
    height: 60vh;
    padding: 20px;
}
.skills h3, p {
    text-align: center;
}

.skill-box {
    background-color: white;
    -moz-box-shadow-bottom: 0 4px 2px -2px rgba(0, 0, 0, 0.308);
    -webkit-box-shadow-bottom:  0 4px 2px -2px rgba(0, 0, 0, 0.308);
     box-shadow: 0 4px 2px -2px rgba(61, 61, 61, 0.308);
     padding: 15px;
     border-radius: 12px;
}
.col-md-4:first-child{
    border-right: 1px solid grey;
}
.col-md-4:last-child{
    border-left: 1px solid grey;
}

ul, li {
    list-style-type: none;
}
.project {
    margin-top: 10px;
    text-align: center;
}
.contact-content {
    margin-top: 20px;
}

.contact-form {
    margin-top: 10px;
}
